import React from 'react';


const AdjustRegionData=(shortNameArray, nameArray, regionNumber, totalAllByDate)=> {
  const fullName=nameArray
  const r8Ok=shortNameArray
  const provName=r8Ok.map((shortName, key)=>
  {return Object.keys(shortName)}
  )
 const r8Accumulate=[{"rAccumulate":0}, {"r8Daily":0}]
 const choosePlot = totalAllByDate? 
	  Object.keys(totalAllByDate).map((thisConfirmDate, key)=>{
		  let r8array=fullName.map((callName,i)=>{
					return {
						[fullName[i]]:0
					}
				})  
		  let r8DailyCases=[{'r8Daily':0}]
		  let prov=Object.keys(totalAllByDate[thisConfirmDate]).map((province, key)=>{
			  fullName.map((callName, i)=>{
			     let fillarray=province===callName&&{[fullName[i]]:totalAllByDate[thisConfirmDate][province]}
				 if (province===callName){
					r8array[i]={[fullName[i]]:totalAllByDate[thisConfirmDate][province]}
					r8DailyCases[0]["r8Daily"]=r8DailyCases[0].r8Daily+totalAllByDate[thisConfirmDate][province]
				 } 
			  }
			  )
			
		})
		let sumDaily=r8DailyCases[0].r8Daily
		let accCases=sumDaily+r8Accumulate[0]["rAccumulate"]
		r8Accumulate[1]={"r8Daily":sumDaily}
		r8Accumulate[0]={"rAccumulate":accCases}		
		let provAcc=[]
		fullName.map((callName, i)=>{
			provAcc[i]=r8array[i][fullName[i]]+r8Ok[i][provName[i]]
			r8Ok[i]={[provName[i]]:provAcc[i]}
		})
		let finalResult={"Date":thisConfirmDate.substr(0, 10).replace(/-/g,'/')}
		fullName.map((callName, i)=>{
			finalResult[fullName[i]]= r8array[i][fullName[i]]
		})
		provName.map((abrName, i)=>{
			finalResult[provName[i]]= provAcc[i]
		})
		finalResult["R"+regionNumber+"NewConfirmed"]=sumDaily
		finalResult["R"+regionNumber+"Confirmed"]=accCases

		return finalResult
	  })
  :[]
	return choosePlot
}
export default AdjustRegionData